import React from 'react'
import { DropTarget } from 'react-dnd'
import { DragDropContext, Droppable, Draggable} from 'react-beautiful-dnd';
import SliceCard from './SliceCard.js';
import { PlusOutlined } from '@ant-design/icons'
import { narrative_spec2cards } from '@/tool/narrative2cards';
import * as api from '@/axios/api';
import config from '@/axios/config';
import ActionType from '@/action/type';
import {notification} from "antd";

// Drag sources and drop targets only interact
// if they have the same string type.
// You want to keep types in a separate file with
// the rest of your app's constants.
const Types = {
    CARD: 'card'
}

export default class DropView extends React.Component{
    constructor(props) {
        super(props);
    }


    addVideoSlice = () => {
        //slice
        this.props.addEmptyCard()
    }

    onDragStart = () => {
        console.log('drag start!')
    }
    
    openNotification = placement => {
        notification.info({
            message: 'Parse Error',
            description:
            'The question cannot be resolved.',
          placement,
        });
    };

    exchangeNotification = placement => {
        notification.info({
            message: 'Exchange Error',
            description:
            'Please exchange two Comparative operators.',
          placement,
        });
    };


    onDragEnd = (result) => {
        console.log('drag end!')

        // dropped outside the list
        if (!result.destination) {
            return;
        }
        let origin = result.source.index;
        let target = result.destination.index;
        if(origin == target)
            return;

        if(this.props.narrative_spec.operators[origin].type=='comparative' && this.props.narrative_spec.operators[target].type=='comparative')
        {
            // this.props.changeCard(origin,target);
            console.log('origin'+origin)
            console.log('target'+target)
            this.props.changeOperator(origin,target);

            if(document.getElementById("pause-btn"))
                document.getElementById("pause-btn").click();

            new Promise(async (resolve, reject) => {
            
                let specOperators = this.props.narrative_spec.operators
                specOperators.splice(specOperators.length-1,1)

                let correction = ''
                specOperators.map((item,index)=>{
                    let argsString = item.args
                    let args1 = argsString.substring(1,argsString.length-1)
                    let args2 = args1.replace(/\[/g,'')
                    let argsA = args2.replace(/\]/g,'')
                    let argsB = argsA.replace(/\'/g,'')
                    let args3 = argsB.replace(/,/g,'').replace(/tbl\|/g,'tbl:').replace(/col\|/g,'col:').replace(/col\|/g,'col:')
                    let args4 = args3.replace(/comparative\|>\|/g,'great/').replace(/comparative\|<\|/g,'less/').replace(/comparative\|>=\|/g,'greatequal/').replace(/comparative\|<=\|/g,'lessequal/').replace(/comparative\|=\|/g,'equal/').replace(/comparative\|!=\|/g,'notequal/')
                    let args5 = args4.replace(/(?<=\/\d+)\D*?(?=col)/g,' ').replace(/\|/g,'/')
                    correction = correction+item.type+' '+args5+', '
                })
                correction = correction.substring(0,correction.length-2) 

                let correctionJSON = {
                    "correction":correction,
                    "operators":this.props.narrative_spec.operators
                }
                let data = {
                    csvname:this.props.fileName,
                    question:this.props.question,
                    correction:correctionJSON
                }
                // api
                
                this.props.isGeneratingGIF(true)                                                                                                                                                                                                                                                            

                var response = await api.question2gif(config.url.question2gif,data)

                if (response.data.error ==  "parse error" ) {
                    console.log('fail')
                    this.openNotification('center')
                    this.props.isGeneratingGIF(false)
                    reject()
                    return
                }

                // reponsenarrative chart
                this.props.isGeneratingGIF(false)
                console.log(response.data)
                this.props.operatorChange(false)
                this.props.animationChange(false)

                this.props.setLeftsiderContent(ActionType.SET_EDIT_SLICE)
                this.props.setPosition(0);
                let percent1 = '0%';
                let div = document.getElementById("timeMarks");
                div.setAttribute('style','background:linear-gradient(90deg,#f28b30 '+percent1+',rgb(246, 246, 247) '+percent1+');height:3px;width:100%;border-radius:15px;margin-top:10px;position: absolute;z-index: 0');
                var tempActions = response.data.actions
                var allCardsTotalTime = tempActions[tempActions.length-1].style.delay+tempActions[tempActions.length-1].style.duration
                this.props.changeAllVideoTime(allCardsTotalTime)
                this.props.changeNowVideoTime(0);

                document.getElementById("play-btn").click();
                
                this.props.question2Gif(this.props.question, response.data)
                this.props.changeTempSpec(response.data)
                

                resolve(response)
                
            })
        }
        else
        {
            this.exchangeNotification('center')
            return;
        }

    }

    render() {
        const { dataresLocal, schemaFilterLocal, narrative_spec, cardItems, videoIndex } = this.props
        const step_actions = narrative_spec2cards(narrative_spec)
        //slice
        const AddEmptySlice = () => {
            return (
                <div className='add-empty-slice' onClick={this.addVideoSlice}>
                    <div className='add-empty-slice-btn'>
                        <PlusOutlined className='add-logo-icon' />
                    </div>
                </div>
            )
        }
        return(
            <div className='video-fact-control'>
                <DragDropContext onDragStart={this.onDragStart} onDragEnd={this.onDragEnd}>
                    <Droppable droppableId="storyline" direction="horizontal">
                        {(provided, snapshot) => (
                            <div className='video-slice'
                                ref={provided.innerRef}
                                {...provided.droppableProps}>

                                    {step_actions.map((item, index) => {
                                    return(
                                        <Draggable key={index} draggableId={index + ''} index={index}>
                                            {(provided, snapshot) => (
                                                <SliceCard
                                                    videoIndex={videoIndex}
                                                    index={index}
                                                    provided={provided}
                                                    spec={item}
                                                    dataresLocal={dataresLocal}
                                                    schemaFilterLocal={schemaFilterLocal}
                                                    {...this.props}
                                                />
                                            )}
                                        </Draggable>
                                    )
                                })}
                                
                                {provided.placeholder}
                            </div>
                        )}
                    </Droppable>
                </DragDropContext>
                {/* <AddEmptySlice /> */}
            </div>
        )
    }
}
